.operation {
  grid-area: 1 / 1 / 2 / 5;
  background-color: white;
  font-weight: 400;
  font-size: 1rem;
  color:grey
}



.result {
  grid-area: 2 / 1 / 3 / 5;
  background-color: white;
  font-weight: 600;
  font-size: 1.5rem;

}

.gridContainer {
  display: grid;
  grid-template-columns: 4rem 4rem 4rem 4rem;
  grid-template-rows: 4rem 4rem 4rem 4rem 4rem 4rem 4rem 4rem;
  justify-content: center;  
}

